import { chatUrls } from '../../service/ChatService';

const registerChatRoutes = (server) => {
    const serverConfig = server;

    serverConfig.namespace = '/v1/api/gbr/';
    const mockGetConfig = () => {
        return {
            hoop: '9AM-5PM',
            isAgentOnline: false,
            ewt: 5,
            isLeaveAgentMessageEnabled: true,
            leaveAgentMessage:
                'Please leave a message and we will get back to you as soon as we can.',
            userInactiveInterval: 10,
            isSuccess: true,
            errors: undefined,
        };
    };
    serverConfig.get('config', mockGetConfig);
    const mockLeaveMessage = () => {
        return {
            success: true,
            errors: undefined,
        };
    };
    serverConfig.post(chatUrls.leaveMessageUrl, mockLeaveMessage);
};

export default registerChatRoutes;
