import cx from 'classnames';
import PropTypes from 'prop-types';
import { PureComponent } from 'react';
import ChooseOptionWindow from './components/choose-option-window/ChooseOptionWindow';
import GenericMessage from './components/generic-message/GenericMessage';
import Hoop from './components/hoop/Hoop';
import Chat from './components/svg/Chat';
import IconButtonChat from './components/icon-button-chat/IconButtonChat';
import ChatService from './service/ChatService';
import './styles/chat-module.scss';

class ChatModule extends PureComponent {
    static propTypes = {
        contextCls: PropTypes.string,
        containerElement: PropTypes.object,
        accountIdentifier: PropTypes.string,
        email: PropTypes.string,
        brand: PropTypes.string,
        env: PropTypes.string,
    };

    state = {
        shouldShowChooseOptionWindow: false,
        shouldShowErrorPage: false,
        shouldShowHoopWindow: false,
        shouldShowMinimizedIconButton: false,
    };

    render() {
        const {
            shouldShowChooseOptionWindow,
            shouldShowErrorPage,
            shouldShowHoopWindow,
            shouldShowMinimizedIconButton,
        } = this.state;

        return (
            <>
                {shouldShowMinimizedIconButton && this.renderIconButton()}
                {shouldShowChooseOptionWindow &&
                    this.renderChooseOptionWindow()}
                {shouldShowHoopWindow && this.renderHoopWindow()}
                {shouldShowErrorPage && this.renderErrorPage()}
            </>
        );
    }

    componentDidMount() {
        this.setContainerElementStyle();
        this.fetchConfig();
    }

    componentWillUnmount() {
        this.restoreContainerElementStyle();
    }

    renderSvgChat = () => <Chat contextCls="svg-container" />;

    renderIconButton = () => {
        return (
            <IconButtonChat
                renderSvg={this.renderSvgChat}
                onClick={this.handleMinimizedIconBtnClick}
            />
        );
    };

    renderHoopWindow = () => {
        const { chatConfigs } = this.state;
        const { contextCls, ...restProps } = this.props;

        return (
            <Hoop
                contextCls={cx(this.baseCls, contextCls)}
                hoop={chatConfigs.hoop}
                isLeaveMsgEnabled={chatConfigs.isLeaveAgentMessageEnabled}
                onCloseBtnClick={this.handleCloseBtnClick}
                {...restProps}
                {...chatConfigs}
            />
        );
    };

    renderChooseOptionWindow = () => {
        const { contextCls, ...restProps } = this.props;
        const { chatConfigs } = this.state;

        return (
            <ChooseOptionWindow
                contextCls={cx(this.baseCls, contextCls)}
                ewt={chatConfigs.ewt}
                {...restProps}
                {...chatConfigs}
                onCloseBtnClick={this.handleCloseBtnClick}
            />
        );
    };

    renderErrorPage = () => {
        const { contextCls, ...restProps } = this.props;

        return (
            <GenericMessage
                contextCls={cx(this.baseCls, contextCls)}
                icon="WarningAlert"
                windowTitle="Chat with an agent"
                headText="There was a problem connecting to chat support"
                subText="Please try again later."
                onCloseBtnClick={this.handleCloseBtnClick}
                {...restProps}
            />
        );
    };

    handleCloseBtnClick = () => {
        this.setState({
            shouldShowMinimizedIconButton: true,
            shouldShowChooseOptionWindow: false,
            shouldShowErrorPage: false,
            shouldShowHoopWindow: false,
        });
    };

    handleMinimizedIconBtnClick = () => {
        this.setState({
            shouldShowMinimizedIconButton: false,
        });
        this.fetchConfig();
    };

    async fetchConfig() {
        const newState = {};

        try {
            const chatConfig = await ChatService.getChatConfig();
            const { isAgentOnline, isSuccess, errors, ...restConfig } =
                chatConfig;
            newState.chatConfigs = restConfig;

            if (isAgentOnline) {
                newState.shouldShowChooseOptionWindow = true;
            } else {
                newState.shouldShowHoopWindow = true;
            }

            if (!isSuccess) {
                newState.shouldShowErrorPage = true;
            }
        } catch (e) {
            newState.shouldShowErrorPage = true;
        } finally {
            this.setState(newState);
        }
    }

    setContainerElementStyle() {
        const { containerElement } = this.props;

        if (containerElement) {
            this.setState({
                originContainerElementPosition: containerElement.style.position,
            });
            containerElement.style.position = 'relative';
        }
    }

    restoreContainerElementStyle() {
        const { containerElement } = this.props;
        const { originContainerElementPosition } = this.state;

        if (containerElement && originContainerElementPosition) {
            containerElement.style.position = originContainerElementPosition;
        }
    }

    baseCls = 'web-chat-module';
}

export default ChatModule;
